import * as React from 'react'
import { StripeProvider } from 'react-stripe-elements'

declare global {
  interface Window { Stripe: any }
}

interface IState {
  stripe: any
}

const stripePushableKey = process.env.REACT_APP_STRIPE_PUSHABLE_KEY as string

export default class extends React.Component<{}, IState> {
  constructor(props: {}) {
    super(props)
    this.state = {
      stripe: null,
    }
  }
  public componentDidMount() {
    if (window.Stripe) {
      this.setState({ stripe: window.Stripe(stripePushableKey) })
    } else {
      const stripeEl = document.querySelector('#stripe-js')
      if (stripeEl) {
        addEventListener('load', () => {
          // Create Stripe instance once Stripe.js loads

          this.setState({ stripe: window.Stripe(stripePushableKey) })
        })
      }
    }
  }
  public render() {
    return (
      <StripeProvider stripe={this.state.stripe}>
        {this.props.children}
      </StripeProvider>
    )
  }
}



