import * as React from 'react'
import { Elements } from 'react-stripe-elements'

import CheckoutForm from './CheckoutForm'
const _CheckoutForm = CheckoutForm as any
const CARD_ELEMENT_OPTIONS = {
  locale: 'no',
  style: {
    base: {
      'fontSize': '18px',
      'color': '#5561c9',
      'letterSpacing': '0.025em',
      '::placeholder': {
        color: '#aab7c4',
      },
    },
    invalid: {
      color: '#9e2146',
    },
  },
}

interface IProps {
  onPay: (token: stripe.TokenResponse) => any
}

class MyStoreCheckout extends React.Component<IProps> {
  public render() {
    return (
      <Elements
        {...CARD_ELEMENT_OPTIONS}
      >
        <_CheckoutForm {...this.props}/>
      </Elements>
    )
  }
}

export default MyStoreCheckout



