import * as React from 'react'
import MyStoreCheckout from './MyStoreCheckout'
import StripeProvider from './StripeProvider'

interface IProps {
  onPay: (token: stripe.TokenResponse) => any
}

export default ({ onPay }: IProps) => (
  <StripeProvider>
    <MyStoreCheckout
      onPay={onPay}
    />
  </StripeProvider>
)



