import * as React from 'react'
import { injectStripe } from 'react-stripe-elements'
import { CardElement } from 'react-stripe-elements'
import { Button, Divider, Label } from 'semantic-ui-react'

interface IProps {
  stripe: stripe.Stripe
  onPay: (token: stripe.TokenResponse) => any
}

interface IState {
  changeProps: stripe.elements.ElementChangeResponse
  blurred: boolean
  isFetching: boolean
}

class CheckoutForm extends React.Component<IConnectProps, IState> {
  public state = {
    changeProps: {
      brand: '',
      complete: false,
      empty: true,
      error: undefined,
    },
    blurred: false,
    isFetching: false,
  }
  public handleSubmit = (ev: any) => {
    const { stripe, onPay } = this.props
    // We don't want to let default form submission happen here, which would refresh the page.
    if (ev) {

      ev.preventDefault()
    }

    // Within the context of `Elements`, this call to createToken knows which Element to
    // tokenize, since there's only one in this group.
    this.setState({ isFetching: true });
    (stripe as any).createToken()
      .then(
        (token: stripe.TokenResponse) => {

          this.setState({ isFetching: false })
          return onPay(token)
        },
      )

    // However, this line of code will do the same thing:
    // this.props.stripe.createToken({type: 'card', name: 'Jenny Rosen'});
  }
  public render() {
    const { complete, error } = this.state.changeProps
    const { blurred, isFetching } = this.state
    const message = error ? (error as any).message : ''
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          Kort detaljer
        <CardElement
            style={{ base: { fontSize: '18px' } }}
            onChange={(changeProps) => this.setState({ changeProps })}
            onBlur={() => this.setState({ blurred: true })}
        />
        </label>
        <Divider hidden={true} />
        <Button
          primary={true}
          disabled={!complete}
          loading={isFetching || this.props.isFetching}
        >
          Bekreft betaling
        </Button>
        {blurred && message && (
          <Label>{message}</Label>
        )}
      </form>
    )
  }
}
import { connect } from 'react-redux'
import { IStoreState } from 'store'
const connectCreator = connect(
  ({ premium }: IStoreState) => {
    const { isFetching } = premium
    return {
      isFetching,
    }
  },
)

type IConnectProps = typeof connectCreator.allProps & IProps
export default injectStripe(connectCreator(CheckoutForm) as any)



